export default {
	data() {
		return {
			exam_list:[],
			exam:'',
			selectKey:[],
			exam_id:''
		}
	},
	onLoad(e) {
		uni.showLoading();
		var t=this;
		t.exam_id=e.id
		var data={
			exam_id:t.exam_id,
			communist_no:t.globalData.userInfo.communist_no,
		}
		t.urlRequest.urlRequest(t.func.examDetail,data,function(res){
			t.exam_list=res.data.exam_list.questions_list;
			t.exam=res.data.exam_list.exam;
		})
		uni.hideLoading();
	},
	methods: {
		answerClick:function(questions_id,key,type,index){
			var t=this;
			var select='';
			var nowSelect=[];
			if(key==0){
				select="A";
			}else if(key==1){
				select="B";
			}else if(key==2){
				select="C";
			}else if(key==3){
				select="D";
			}else if(key==4){
				select="E";
			}else if(key==5){
				select="F";
			}else if(key==6){
				select="G";
			}else if(key==7){
				select="H";
			}
			if(t.selectKey.length<=0){
				if(type=='checkbox'){
					t.exam_list[index]['questions_item'][key]['checked']=t.exam_list[index]['questions_item'][key]['checked']==true?false:true;
				}
				nowSelect={questions_id:questions_id,select:select};
				t.selectKey.push(nowSelect);
			}else{
				var list=t.selectKey;
				var is_have=false;
				if(type=='radio'){
					// 单选
					for(var i=0;i<list.length;i++){
						if(list[i]['questions_id']==questions_id){
							list[i]={questions_id:questions_id,select:select};
							is_have=true;
						}
					}
					if(is_have==false){
						nowSelect={questions_id:questions_id,select:select};
						t.selectKey.push(nowSelect);
					}
				}else{
					//多选
					t.exam_list[index]['questions_item'][key]['checked']=t.exam_list[index]['questions_item'][key]['checked']==true?false:true;
					for(var i=0;i<list.length;i++){
						//如果题目已经选择
						if(list[i]['questions_id']==questions_id){
							if(list[i]['select'].length<=0){
								list.splice(i,1);
								// 判断当前是否已选答案，当无答案时
								nowSelect={questions_id:questions_id,select:select};
								t.selectKey.push(nowSelect);
								is_have=true;
							}else{
								//当有答案时判断所选答案是否存在
								var arr=list[i]['select'].split(',');
								for(var j=0;j<arr.length;j++){
									if(arr[j]==select){
										// 如果选项已经存在则删除
										arr.splice(j,1);
										is_have=true;
										select=arr.join(',');
										//判断删除后是否有数据，没有的话删除数组
										if(select==''){
											list.splice(i,1);
										}else{
											nowSelect={questions_id:questions_id,select:select};
											list[i]=nowSelect;
										}
										
									}
								}
								if(is_have==false){
									arr.push(select);
									select=arr.join(',');
									nowSelect={questions_id:questions_id,select:select};
									list[i]=nowSelect;
									is_have=true;
								}
							}
						}
					}
					if(is_have==false){
						nowSelect={questions_id:questions_id,select:select};
						list.push(nowSelect);
						is_have=true;
					}
				}
			}
		},
		subAnswer:function(e){
			var t=this;
			if(t.exam_list.length!=t.selectKey.length){
				uni.showModal({
					title: '提示',
					content: '您还有未选择的题目，确定提交吗？',
					success: function (res) {
						if (res.confirm) {
							t.submitContent()
						} else if (res.cancel) {
							console.log('取消');
						}
					}
				});
			}else{
				t.submitContent()
			}
			
		},
		submitContent:function(){
			var t=this;
			var arr='';
			var list=t.selectKey;
			uni.showLoading();
			for(var i=0;i<list.length;i++){
				list[i]=JSON.stringify(list[i])
			}
			
			var data={
				exam_id:t.exam_id,
				communist_no:t.globalData.userInfo.communist_no,
				question_arr:list.join('|',list)
			}
			t.urlRequest.urlRequest(t.func.examSubmit,data,function(res){
				if(res.data.status==1){
					uni.showToast({
						title: res.data.msg,
						duration: 2000,
						icon:'none', 
						success:function(){
							setTimeout(function(){
								uni.navigateTo({
									url: "../index/test-center"
								})
							},2000)
						}
					});
				}else{
					uni.showToast({
						title: res.data.msg,
						duration: 2000,
						icon:'none', 
					});
				}
				
				uni.hideLoading();
			})
		}
	}
}
